import React from "react";
import {
    ContentfulRichTextGatsbyReference,
    RenderRichTextData,
} from "gatsby-source-contentful/rich-text";
import "./TimelineBlock.scss";
import { RenderRichContent } from "./RenderRichContent";
import EventSVG from '../../public/images/event.svg';
import { GatsbyImage, IGatsbyImageData } from "gatsby-plugin-image";

type TimelineProps = {
    data: {
        title: string;
        description: RenderRichTextData<ContentfulRichTextGatsbyReference>;
        year: string;
        image: IGatsbyImageData;
    }[];
};
export const TimelineBlock = ({ data }: TimelineProps) => {
    return (
        <section className="ps-timeline-sec">
            <div className="container" style={{ 'width': 350 * data.length + 'px' }}>
                <ol className="ps-timeline">
                    {data.map((item,index) => {
                        return (
                            <li key={index}>
                                <div className={`img-handler-${index%2==0?'top':'bot'}`}>
                                    {/* <GatsbyImage
                                        className="image"
                                        image={item.image}
                                        
                                        alt="event image"
                                    /> */}
                                    <GatsbyImage image={EventSVG} alt="event image"/>
                                </div>
                                <div className={`ps-${index%2==0?'bot':'top'}`}>
                                    <div className="title">{item.title}</div>
                                    <div className="description"><RenderRichContent
                                        content={item.description}
                                    /></div>
                                </div>
                                <span className={`ps-sp-${index%2==0?'top':'bot'}`}>{item.year}</span>
                            </li>
                        )
                    })}
                </ol>
            </div>
        </section>
    );
}