import React from "react";
// import Img from "gatsby-image"
import {
  ContentfulRichTextGatsbyReference,
  renderRichText,
  RenderRichTextData,
} from "gatsby-source-contentful/rich-text";

type RenderRichContentProps = {
  content: RenderRichTextData<ContentfulRichTextGatsbyReference>;
  className?: string;
};

export const RenderRichContent = ({ content, className }: RenderRichContentProps) => {
  // Add custom options to `renderRichText` function if necessary
  return <div className={className}>{content && renderRichText(content)}</div>;
};
