import React from "react";
import "./AuthorBlock.scss";
import linkedInLogo from "../../public/images/linkedin.svg";
import { GatsbyImage, IGatsbyImageData } from "gatsby-plugin-image";

type AuthorBlockProps = {
    className?: string;
    data: {
        title: string;
        function: string;
        email: string;
        image?: IGatsbyImageData;
        imageAlt: string;
        linkedIn: string;
    };
};
// Height of feature image should be 400px * mandatory => Use graphql for img height ?h=400
export const AuthorBlock = ({ data, className }: AuthorBlockProps) => {
    return (
        <div className={className} ><div className="face-block">
            {data.image ? (<GatsbyImage
                image={data.image}
                alt={data.imageAlt}
            />) : <></>}
            <div className="name">{data.title}</div>
            <div className="function">{data.function}</div>
            <div className="email">{data.email}</div>
            {data.linkedIn ? (
                <a href={data.linkedIn} target="_blank" className="linked-in"><GatsbyImage image={linkedInLogo} alt="linkedin profile" /></a>) : <></>}
        </div><div className="spacer"></div>
        </div>
    )
}